.p-content-box {
  display: flex;
  flex-wrap: wrap;
}

.p-content-box__item {
  position: relative;
  flex: 0 0 50%;



  @include mq(tab) {
    flex: 0 0 25%;
  }
}

.p-content-box__link{
  display: inline-block;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .3s;
    }

    &:hover::after {
      background: rgba($color: #ffffff, $alpha: 0.6);

    }
  
}

.p-content-box__link img {

}

.p-content-box__text {
  font-size: rem(20);
  font-weight: 400;
  letter-spacing: 0.1em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;

  @include mq(tab) {
    font-size: rem(25);  
    letter-spacing: 0.08;
  }
  
}